<template>
  <div class="swiper-container" :class="swipeid">
      <div class="swiper-wrapper">
          <slot name="swiper-icon"></slot>
      </div>
      <div :class='{"swiper-pagination":pagination}' :style='{"text-align":paginationDirection}'></div>
  </div>
</template>

<script>
import '../assets/js/swiper.js'
export default {
    props:{
      swipeid:{         //
        type:String,
        default:''
      },
      effect:{          //
        type:String,
        default:'slide'
      },
      loop:{            //
        type:Boolean,
        default:true
      },
      direction:{       //
        type:String,
        default:'horizontal'
      },
      autoplay:{          //
        type:String,
        default:'2000'
      },
      paginationType:{      //
        type:String,
        default:'bullets'
      },
      pagination:{        //
        type:Boolean,
        default:false
      },
      paginationDirection:{       //
        type:String,
        default:'center'
      }
    },

  mounted:function(){
    var that=this;
    new Swiper('.'+that.swipeid,{
      loop:that.loop,
      pagination:'.swiper-pagination',
      paginationType:that.paginationType,
      autoplay:that.autoplay,
      direction:that.direction,
      effect:that.effect,
      autoplayDisableOnInteraction:false                   //
    })
  }

}
</script>

<style>
@import "../assets/css/swiper.min.css";
</style>



